import PayCommon from '@/components/PayCommon'
export default {
    data() {  
        return {
            chosenCoupon: -1, showList: false, imgs:[require('../../assets/img/checke.png'),require('../../assets/img/checkedRed.png')],isChecked: false,
            redMoneys: { couponId:'', price:0 }
        }
    },
    components: {
        'PayCommon-item': PayCommon
    },
    beforeCreate(){
        this.$store.dispatch('orderDetail', this.$route.query.orderId)
        this.$store.dispatch('user')
        this.$store.dispatch('mySelfRedPacketList')
    },
    computed:{
        orderDetail(){
            return this.$store.state.orderDetail
        },
        coupons(){
            let list = this.$store.state.mySelfRedPacketList, array = []
            for(let val of list){
                array.push({
                    available: 1,
                    discount: 0,
                    denominations: val.price*100,
                    originCondition: val.use_codition == 0 ? 0 : val.condition_price*100,
                    reason: '',
                    value: 150,
                    name: val.title,
                    startAt: new Date(val.limit_date_start.replace(/-/g, "/")).getTime()/1000,
                    endAt: new Date(val.limit_date_end.replace(/-/g, "/")).getTime()/1000,
                    id: val.id
                })
            }
            // this.coupons = array
            return array
        },
        user(){
            return this.$store.state.user
        },
        isFloats(){
            if(this.user){
                return Number.isInteger(this.user.wtCustomer.amount) ? `${this.user.wtCustomer.amount}.00` : this.user.wtCustomer.amount
            }
        },
        moeny(){
            return 0 > this.orderDetail.ORDER_MONEY - this.redMoneys.price ? 0 :(this.orderDetail.ORDER_MONEY - this.redMoneys.price).toFixed(2)
        },
        isPays(){
            return this.$store.state.isPay
        }
    },
    created(){
        document.body.scrollTop = 0
        document.documentElement.scrollTop = 0
        document.title = '订单确认'
        // console.log(12)
    },
    activated(){
        // console.log('hah')
    },
    methods:{
        address(){
            this.$router.push('/Address')
        },
        onChange(index) {
            this.showList = false;
            this.chosenCoupon = index;
            if(index > -1){
                let list = { redPacketId: this.coupons[index].id, orderMoney: this.orderDetail.ORDER_MONEY }
                this.$store.dispatch('validationRedPacket', list)
                .then(res =>{
                    // console.log(res.data)
                    if(res.data.code == 200){
                        this.redMoneys = res.data.data
                    }else{
                        this.$toast(res.data.message)
                    }
                })
                .catch(err =>{})
            }else{
                this.redMoneys.price = 0
            }
        },
        isPay(){
            if(!this.isChecked){
                if(this.user.wtCustomer.payPassword == '' || this.user.wtCustomer.payPassword == null){
                    this.$dialog.confirm({
                        title: '您还没有设置密码！',
                        confirmButtonText:'去设置'
                    }).then(() => {
                        this.$router.push({path:'/Setpassword'})
                    }).catch(()=>{

                    });
                    return
                }
            }
            this.$store.commit('SET_PAY', true)
        },
    },
}



